import axios from 'axios';
import { URL_API } from '../environments/environment';

export default class AlmacenService {
    constructor() {
        this.urlCategoria = `${URL_API}categoria/`;
        this.urlProducto = `${URL_API}articulo/`;
        this.config = {
            headers: {
                token: localStorage.getItem('token')
            }
        }
    }
    // Categorias
    getCategorias = (value) => {
        return axios.get(`${this.urlCategoria}list/${value}`, this.config).then(res => Promise.resolve(res)).catch(err => Promise.reject(err));
    }
    getCategoria = (id) => {
        return axios.get(this.urlCategoria + 'query/' + id, this.config).then(res => Promise.resolve(res)).catch(err => Promise.reject(err));
    }
    postCategoria = (nombre, descripcion) => {
        let body = { nombre, descripcion };
        return axios.post(`${this.urlCategoria}add`, body, this.config).then(res => Promise.resolve(res)).catch(err => Promise.reject(err));
    }
    putCategoria = (_id, nombre, descripcion) => {
        let body = { _id, nombre, descripcion };
        return axios.put(`${this.urlCategoria}update`, body, this.config).then(res => Promise.resolve(res)).catch(err => Promise.reject(err));
    }
    deleteCategoria = (id) => {
        return axios.delete(`${this.urlCategoria}remove/${id}`, this.config).then(res => Promise.resolve(res)).catch(err => Promise.reject(err));
    }
    // Productos
    getProductos = (value) => {
        return axios.get(`${this.urlProducto}list/${value}`, this.config).then(res => Promise.resolve(res)).catch(err => Promise.reject(err));
    }
    getProducto = (id) => {
        return axios.get(this.urlProducto + 'query/' + id, this.config).then(res => Promise.resolve(res)).catch(err => Promise.reject(err));
    }
    postProducto = (categoria, codigo, nombre, descripcion, precio_venta, stock) => {
        let body = { categoria, codigo, nombre, descripcion, precio_venta, stock };
        return axios.post(`${this.urlProducto}add`, body, this.config).then(res => Promise.resolve(res)).catch(err => Promise.reject(err));
    }
    putProducto = (_id, categoria, codigo, nombre, descripcion, precio_venta, stock) => {
        let body = { _id, categoria, codigo, nombre, descripcion, precio_venta, stock };
        return axios.put(`${this.urlProducto}update`, body, this.config).then(res => Promise.resolve(res)).catch(err => Promise.reject(err));
    }
    deleteProducto = (id) => {
        return axios.delete(`${this.urlProducto}remove/${id}`, this.config).then(res => Promise.resolve(res)).catch(err => Promise.reject(err));
    }
}