import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import AuthService from './services/AuthService';
import Login from './components/Login';
import Nav from './components/Nav';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.auth = new AuthService();
    this.state = { islogged: this.auth.loggedIn() };
  }

  handleChangeAuth = () => {
    this.setState({ islogged: this.auth.loggedIn() });
  }

  render() {
    if (this.state.islogged) {
      return (
        <Nav handleChangeAuth={this.handleChangeAuth} />
      )
    } else {
      return (

        <Router>
          <Switch>
            {/* <Route path="/login" component={Login}></Route> */}
            <Route path="/Login" render={routeProps => <Login {...routeProps} handleChangeAuth={this.handleChangeAuth} />}></Route>
            {/* <Route path="/register" component={Register}></Route> */}
            <Redirect from='*' to='/Login' />
          </Switch>
        </Router>
        //   <Login handleChangeAuth={this.handleChangeAuth} />
      )
    }
  }
}

