import React, { Component } from 'react'
import { Grid, Button, Card, Input } from 'semantic-ui-react'
import AuthService from '../services/AuthService';
import Swal from 'sweetalert2';

export default class Login extends Component {

    constructor(props) {
        super(props);
        this.Auth = new AuthService();
        this.state = { email: '', password: '' };
        this.handleChangeAuth = props.handleChangeAuth;
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.Auth.login(this.state.email, this.state.password)
            .then(res => {
                this.Auth.setToken(res.data.token);
                this.Auth.setUser(res.data.user);
                this.handleChangeAuth();
                Swal.fire('Login', `Bienvenido ${res.data.user.nombre}`, 'success');
            }).catch(err => {
                if (err.response.status === 404) {
                    Swal.fire('Login', 'Credenciales Erroneas', 'error');
                } else {
                    Swal.fire('Login', 'Error interno, comuniquese con el proveedor', 'error');
                }
            });
    }

    render() {
        return (
            <Grid columns={3}>
                <Grid.Row>
                    <Grid.Column />
                    <Grid.Column width={6}>
                        <Card.Group style={{ width: '500px', height: '300px', marginLeft: 'calc(50% - 250px)', marginTop: '30%' }} >
                            <Card fluid>
                                <Card.Content>
                                    <Card.Header textAlign='center'>Formulario de Login</Card.Header>
                                </Card.Content>
                                <Card.Content>
                                    <form onSubmit={this.handleSubmit}>
                                        <Input icon='mail' iconPosition='left' fluid type="text" name="email" placeholder='Usuario' onChange={this.handleChange} />
                                        <br />
                                        <Input icon='slack hash' iconPosition='left' fluid type='password' name="password" placeholder='ContraseÃ±a' onChange={this.handleChange} />
                                        <br />
                                        <Button fluid basic color='green'>Entrar</Button>
                                    </form>
                                </Card.Content>
                            </Card>
                        </Card.Group>
                    </Grid.Column>
                    <Grid.Column />
                </Grid.Row>
            </Grid>

        )
    }
}
