import React, { Component } from 'react';
import { Button, Card, Input, Form } from 'semantic-ui-react'
import UsuarioService from '../services/UsuarioService';
import Swal from 'sweetalert2';

export default class Profile extends Component {
    constructor(props) {
        super(props);
        this.Usuario = new UsuarioService();
        this.state = { rol: '', nombre: '', tipo_documento: '', num_documento: '', direccion: '', telefono: '', email: '' };
        this.getUser();
    }

    getUser = () => {
        this.Usuario.getProfile().then(res => {
            this.setState({
                rol: res.data.rol,
                nombre: res.data.nombre,
                tipo_documento: res.data.tipo_documento,
                num_documento: res.data.num_documento,
                direccion: res.data.direccion,
                telefono: res.data.telefono,
                email: res.data.email
            });
        }).catch(err => {
            if (err.response.status === 404) {
                Swal.fire('Perfil', 'No se ha podido cargar su informacion', 'error');
            } else {
                Swal.fire('Error', 'Se genero un error interno, por favor avisar a soporte', 'error');
            }
            console.log(err.response)
        });
    }
    handleSubmit = (e) => {
        e.preventDefault();
        console.log(this.state);
    }
    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    render() {
        // const selectDocumento = [
        //     { key: 'CC', value: 'CC', text: 'CC' },
        //     { key: 'TI', value: 'TI', text: 'TI' },
        //     { key: 'DE', value: 'DE', text: 'DE' },
        // ]
        // const selectCargo = [
        //     { key: 'Administrador', value: 'Administrador', text: 'Administrador' },
        //     { key: 'Almacenero', value: 'Almacenero', text: 'Almacenero' },
        //     { key: 'Vendedor', value: 'Vendedor', text: 'Vendedor' },
        // ];
        return (
            <div style={{ width: '500px', height: '300px', marginLeft: 'calc(50% - 250px)', marginTop: '5%' }}>
                <Card fluid>
                    <Card.Content>
                        <Card.Header textAlign='center'>Perfil</Card.Header>
                    </Card.Content>
                    <Card.Content>
                        <Form onSubmit={this.handleSubmit}>
                            <Form.Field>
                                <Input fluid type="text" name="nombre" value={this.state.nombre} placeholder='Nombre Completo' onChange={this.handleChange} />
                            </Form.Field>
                            <Form.Field>
                                <Input fluid type="text" name="tipo_documento" value={this.state.tipo_documento} placeholder='Tipo de Documento' onChange={this.handleChange} />
                            </Form.Field>
                            <Form.Field>
                                <Input fluid type="number" name="num_documento" value={this.state.num_documento} placeholder='Numero de identificacion' onChange={this.handleChange} />
                            </Form.Field>
                            <Form.Field>
                                <Input fluid type="text" name="rol" value={this.state.rol} placeholder='Cargo' onChange={this.handleChange} />
                            </Form.Field>
                            <Form.Field>
                                <Input fluid type="text" name="direccion" value={this.state.direccion} placeholder='Direccion de residencia' onChange={this.handleChange} />
                            </Form.Field>
                            <Form.Field>
                                <Input fluid type="number" name="telefono" value={this.state.telefono} placeholder='Numero de contacto' onChange={this.handleChange} />
                            </Form.Field>
                            <Form.Field>
                                <Input fluid type="email" name="email" value={this.state.email} placeholder="Correo electronico" onChange={this.handleChange} />
                            </Form.Field>
                            <Form.Field>
                                <Button fluid basic color='green'>Guardar</Button>
                            </Form.Field>
                        </Form>
                    </Card.Content>
                </Card>
            </div>
        )
    }
}
