import React, { Component } from 'react';
import { Grid, Form, Container, Card, Input, TextArea, Button, Table, Modal } from 'semantic-ui-react';
import AlmacenService from '../../services/AlamacenService';
import Swal from 'sweetalert2';

export default class Producto extends Component {
    constructor(props) {
        super(props);
        this.Almacen = new AlmacenService();
        this.state = {
            id: '', n_categoria: '', categoria: '', codigo: '', nombre: '', descripcion: '', precio_venta: '', stock: '', articulos: [], categorias: [], modal: false
        };
        this.getArticulos();
        this.getCategorias();
    }

    getCategorias = async (e) => {
        let value = '';
        (e) ? value = e.target.value : value = '';
        await this.Almacen.getCategorias(value)
            .then(res => {
                this.setState({ categorias: res.data })
            }).catch(err => {
                console.log(err.response);
            });
    }
    getArticulos = async (e) => {
        let value = '';
        (e) ? value = e.target.value : value = '';
        await this.Almacen.getProductos(value)
            .then(res => {
                this.setState({ articulos: res.data });
            }).catch(err => {
                console.log(err.response);
            });
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    handleSubmit = (e) => {
        e.preventDefault();
        if (this.state.id === '') {
            this.Almacen.postProducto(this.state.categoria, this.state.codigo, this.state.nombre, this.state.descripcion, this.state.precio_venta, this.state.stock)
                .then(res => {
                    this.getArticulos();
                    Swal.fire('Producto', `Producto ${this.state.nombre} agregado`, 'success');
                    this.cleanForm();
                }).catch(err => {
                    console.log(err.response);
                    Swal.fire('Producto', `Producto ${this.state.nombre} no pudo ser agregado`, 'error');
                });
        } else {
            this.Almacen.putProducto(this.state.id, this.state.categoria, this.state.codigo, this.state.nombre, this.state.descripcion, this.state.precio_venta, this.state.stock)
                .then(res => {
                    this.getArticulos();
                    Swal.fire('Producto', `Producto ${this.state.nombre} Actualizado`, 'success');
                    this.cleanForm();
                }).catch(err => {
                    console.log(err.response);
                    Swal.fire('Producto', `Producto ${this.state.nombre} no pudo ser actualizado`, 'error');
                });
        }
    }
    handleSelect = async (data) => {
        await this.setState({
            id: data._id,
            categoria: data.categoria._id,
            n_categoria: data.categoria.nombre,
            codigo: data.codigo,
            nombre: data.nombre,
            descripcion: data.descripcion,
            precio_venta: data.precio_venta,
            stock: data.stock
        });
    }
    handleSelectCategoria = async (data) => {
        await this.setState({
            n_categoria: data.nombre,
            categoria: data._id,
        });
        this.showModal();
    }
    handleDelete = async (data) => {

        await Swal.fire({
            title: 'Articulo',
            text: `Seguro que desea eliminar ${data.nombre}`,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Si, Eliminar',
            cancelButtonText: 'Cancelar'
        }).then((result) => {
            if (result.value) {
                this.Almacen.deleteProducto(data._id)
                    .then(res => {
                        this.getArticulos();
                        Swal.fire('Producto', `Producto ${data.nombre} eliminado`, 'success');
                        this.cleanForm();
                    }).catch(err => {
                        Swal.fire('Producto', `Producto ${this.data.nombre} no puedo ser eliminado`, 'success');
                    });
            }
        });
    }
    cleanForm = async (e) => {
        await this.setState({
            id: null,
            categoria: '',
            codigo: '',
            nombre: '',
            descripcion: '',
            precio_venta: '',
            stock: '',
            n_categoria: ''
        });
    }
    showModal = async () => {
        await this.setState({ modal: !this.state.modal });
    }

    render() {
        return (
            <Grid columns={2} >
                <Grid.Row>
                    <Grid.Column>
                        <Card fluid>
                            <Card.Content>
                                <Card.Header onClick={this.cleanForm}>Producto</Card.Header>
                            </Card.Content>
                            <Card.Content>
                                <Container>
                                    <Form>
                                        <Form.Field>
                                            <label>Categoria</label>
                                            <Input name="n_categoria" value={this.state.n_categoria} onChange={this.handleChange} placeholder="Categoria" required action >
                                                <input />
                                                <Button onClick={this.showModal}>Buscar</Button>
                                            </Input>
                                        </Form.Field>
                                        <Form.Field>
                                            <label>Codigo</label>
                                            <Input name="codigo" value={this.state.codigo} onChange={this.handleChange} placeholder="Codigo de barras" required />
                                        </Form.Field>
                                        <Form.Field>
                                            <label>Nombre</label>
                                            <Input name="nombre" value={this.state.nombre} onChange={this.handleChange} placeholder="Nombre" required />
                                        </Form.Field>
                                        <Form.Field>
                                            <label>Descripcion</label>
                                            <TextArea fluid="true" name="descripcion" value={this.state.descripcion} onChange={this.handleChange} placeholder="descripcion" />
                                        </Form.Field>
                                        <Form.Field>
                                            <label>Precio</label>
                                            <Input name="precio_venta" value={this.state.precio_venta} onChange={this.handleChange} placeholder="Precio" required />
                                        </Form.Field>
                                        <Form.Field>
                                            <label>Stock</label>
                                            <Input name="stock" value={this.state.stock} onChange={this.handleChange} placeholder="Stock" required />
                                        </Form.Field>
                                        <Grid columns={2} >
                                            <Grid.Row>
                                                <Grid.Column>
                                                    <Form.Field>
                                                        <Button fluid color='yellow' onClick={this.cleanForm}>Limpiar</Button>
                                                    </Form.Field>
                                                </Grid.Column>
                                                <Grid.Column>
                                                    <Form.Field>
                                                        <Button fluid color='green' onClick={this.handleSubmit}>Guardar</Button>
                                                    </Form.Field>
                                                </Grid.Column>
                                            </Grid.Row>
                                        </Grid>
                                    </Form>
                                </Container>
                            </Card.Content>
                        </Card>
                    </Grid.Column>
                    <Grid.Column>
                        <Input fluid name="value" icon='search' placeholder='Buscar categoria' onChange={this.getArticulos} />
                        <Table celled padded>
                            <Table.Header>
                                <Table.Row>
                                    <Table.HeaderCell>Nombre</Table.HeaderCell>
                                    <Table.HeaderCell>Descripcion</Table.HeaderCell>
                                    <Table.HeaderCell>Opciones</Table.HeaderCell>
                                </Table.Row>
                            </Table.Header>

                            <Table.Body>
                                {this.state.articulos.map((text) =>
                                    (
                                        <Table.Row key={text._id} >
                                            <Table.Cell>{text.nombre}</Table.Cell>
                                            <Table.Cell>{text.descripcion}</Table.Cell>
                                            <Table.Cell>
                                                <Grid columns={2} >
                                                    <Grid.Row>
                                                        <Grid.Column>
                                                            <Form.Field>
                                                                <Button fluid color='blue' onClick={() => this.handleSelect(text)}>Editar</Button>
                                                            </Form.Field>
                                                        </Grid.Column>
                                                        <Grid.Column>
                                                            <Form.Field>
                                                                <Button fluid color='red' onClick={() => this.handleDelete(text)}>Eliminar</Button>
                                                            </Form.Field>
                                                        </Grid.Column>
                                                    </Grid.Row>
                                                </Grid>
                                            </Table.Cell>
                                        </Table.Row>
                                    )
                                )}
                            </Table.Body>
                        </Table>
                    </Grid.Column>
                </Grid.Row>

                <Modal open={this.state.modal} onClose={this.showModal}>
                    <Modal.Header>Categorias</Modal.Header>
                    <Modal.Content>
                        <Table celled padded>
                            <Table.Header>
                                <Table.Row>
                                    <Table.HeaderCell>Nombre</Table.HeaderCell>
                                    <Table.HeaderCell>Descripcion</Table.HeaderCell>
                                    <Table.HeaderCell>Opciones</Table.HeaderCell>
                                </Table.Row>
                            </Table.Header>
                            <Table.Body>
                                {this.state.categorias.map((text) =>
                                    (
                                        <Table.Row key={text._id} >
                                            <Table.Cell>{text.nombre}</Table.Cell>
                                            <Table.Cell>{text.descripcion}</Table.Cell>
                                            <Table.Cell>
                                                <Form.Field>
                                                    <Button fluid color='blue' onClick={() => this.handleSelectCategoria(text)}>Seleccionar</Button>
                                                </Form.Field>
                                            </Table.Cell>
                                        </Table.Row>
                                    )
                                )}
                            </Table.Body>
                        </Table>
                    </Modal.Content>
                    <Modal.Actions>
                        <Button negative onClick={this.showModal}>No</Button>
                    </Modal.Actions>
                </Modal>
            </Grid >
        )
    }
}
