import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Redirect, Link, Switch } from 'react-router-dom';

// UI
import { Menu, Icon } from 'semantic-ui-react'
// Servicios
import AuthService from '../services/AuthService';
// Components
import Almacen from './Almacen';
import Compra from './Compra';
import Usuario from './Usuario';
import Venta from './Venta';
import Profile from './Profile';

export default class Nav extends Component {
    constructor(props) {
        super(props);
        this.auth = new AuthService();
        this.state = { visible: false, cargo: this.auth.getCargo() };
        this.handleChangeAuth = props.handleChangeAuth;
        this.sections = (this.state.cargo === 'Administrador')
            ? ['Inicio', 'AlmacÃ©n', 'Compras', 'Ventas', 'Accesos', 'Consultas'] :
            (this.state.cargo === 'Vendedor') ? ['Inicio', 'Ventas'] : ['Inicio', 'AlmacÃ©n', 'Compras'];
        this.menus = [
            { icon: 'home', link: '/' },
            { icon: 'archive', link: '/Almacen' },
            { icon: 'shop', link: '/Compras' },
            { icon: 'barcode', link: '/Ventas' },
            { icon: 'users', link: '/Accesos' },
            { icon: 'search', link: '/Consultas' }
        ];
    }

    handleAuth = (e) => {
        this.auth.loggedOut();
        this.handleChangeAuth();
    }

    render() {
        return (
            <Router>
                <Menu pointing>
                    {this.sections.map((text, index) => (
                        // console.log({ text, index, menu: this.menus[index].link, icon: this.menus[index].icon });
                        <Link to={this.menus[index].link} key={text + '_link'}>
                            <Menu.Item key={text}
                                name={text}
                                onClick={this.handleItemClick}>
                                <Icon disabled name={this.menus[index].icon} />
                                {text}
                            </Menu.Item>
                        </Link>
                    ))}
                    <Menu.Item position='right'
                        name='Salir'
                        onClick={this.handleAuth}>
                        <Icon disabled name="log out" placeholder="Salir" />
                        Salir
                    </Menu.Item>
                </Menu>
                <Switch>
                    <Route path="/" exact render={routeProps => <Profile {...routeProps} propname={'p'} />}></Route>
                    <Route path="/Almacen" exact render={routeProps => <Almacen {...routeProps} propname={'p'} />}></Route>
                    <Route path="/Compras" exact render={routeProps => <Compra {...routeProps} propname={'p'} />}></Route>
                    <Route path="/Ventas" exact render={routeProps => <Venta {...routeProps} propname={'p'} />}></Route>
                    <Route path="/Accesos" exact render={routeProps => <Usuario {...routeProps} propname={'p'} />}></Route>
                    {/* <Route path="/Consultas" exact render={routeProps =c> <Reports {...routeProps} propname={'p'} />}></Route> */}
                    <Redirect from='*' to='/' />
                </Switch>
            </Router>

        )
    }
}
