import React, { Component } from 'react';
import { Grid, Form, Container, Card, Input, TextArea, Button, Table } from 'semantic-ui-react';
import AlmacenService from '../../services/AlamacenService';
import Swal from 'sweetalert2';

export default class Categoria extends Component {
    constructor(props) {
        super(props);
        this.Almacen = new AlmacenService();
        this.state = { id: '', nombre: '', descripcion: '', categorias: [] };
        this.getCategorias();
    }

    getCategorias = async (e) => {
        let value = '';
        (e) ? value = e.target.value : value = '';
        await this.Almacen.getCategorias(value)
            .then(res => {
                this.setState({ categorias: res.data })
            }).catch(err => {
                console.log(err.response);
            });
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    handleSubmit = (e) => {
        e.preventDefault();
        if (this.state.id === '') {
            this.Almacen.postCategoria(this.state.nombre, this.state.descripcion)
                .then(res => {
                    this.getCategorias();
                    Swal.fire('Categoria', `Categoria ${this.state.nombre} agregada`, 'success');
                    this.cleanForm();
                }).catch(err => {
                    console.log(err.response);
                });
        } else {
            this.Almacen.putCategoria(this.state.id, this.state.nombre, this.state.descripcion)
                .then(res => {
                    this.getCategorias();
                    Swal.fire('Categoria', `Categoria ${this.state.nombre} Actualizada`, 'success');
                    this.cleanForm();
                }).catch(err => {
                    console.log(err.response);
                });
        }
    }

    handleSelect = async (data) => {
        await this.setState({
            nombre: data.nombre,
            descripcion: data.descripcion,
            id: data._id
        });
    }
    handleDelete = async (data) => {

        await Swal.fire({
            title: 'Categoria',
            text: `Seguro que desea eliminar ${data.nombre}`,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Si, Eliminar',
            cancelButtonText: 'Cancelar'
        }).then((result) => {
            if (result.value) {
                this.Almacen.deleteCategoria(data._id)
                    .then(res => {
                        this.getCategorias();
                        Swal.fire('Categoria', `Categoria ${data.nombre} eliminada`, 'success');
                        this.cleanForm();
                    }).catch(err => {
                        Swal.fire('Categoria', `Categoria ${this.data.nombre} no puedo ser eliminada`, 'success');
                    });
            }
        });
    }

    cleanForm = (e) => {
        this.setState({
            nombre: '',
            descripcion: '',
            id: ''
        });
    }

    render() {
        return (
            <Grid columns={2} >
                <Grid.Row>
                    <Grid.Column>
                        <Card fluid>
                            <Card.Content>
                                <Card.Header>Categoria</Card.Header>
                            </Card.Content>
                            <Card.Content>
                                <Container>
                                    <Form>
                                        <Form.Field>
                                            <label>Nombre</label>
                                            <Input name="nombre" value={this.state.nombre} onChange={this.handleChange} placeholder="Nombre" required />
                                        </Form.Field>
                                        <Form.Field>
                                            <label>Descripcion</label>
                                            <TextArea fluid="true" name="descripcion" value={this.state.descripcion} onChange={this.handleChange} placeholder="descripcion" />
                                        </Form.Field>
                                        <Grid columns={2}>
                                            <Grid.Row>
                                                <Grid.Column>
                                                    <Form.Field>
                                                        <Button fluid color='yellow' onClick={this.cleanForm}>Limpiar</Button>
                                                    </Form.Field>
                                                </Grid.Column>
                                                <Grid.Column>
                                                    <Form.Field>
                                                        <Button fluid color='green' onClick={this.handleSubmit}>Guardar</Button>
                                                    </Form.Field>
                                                </Grid.Column>
                                            </Grid.Row>
                                        </Grid>
                                    </Form>
                                </Container>
                            </Card.Content>
                        </Card>
                    </Grid.Column>
                    <Grid.Column>
                        <Input fluid name="value" icon='search' placeholder='Buscar categoria' onChange={this.getCategorias} />
                        <Table celled padded>
                            <Table.Header>
                                <Table.Row>
                                    <Table.HeaderCell>Nombre</Table.HeaderCell>
                                    <Table.HeaderCell>Descripcion</Table.HeaderCell>
                                    <Table.HeaderCell>Opciones</Table.HeaderCell>
                                </Table.Row>
                            </Table.Header>
                            <Table.Body>
                                {this.state.categorias.map((text) =>
                                    (
                                        <Table.Row key={text._id} >
                                            <Table.Cell>{text.nombre}</Table.Cell>
                                            <Table.Cell>{text.descripcion}</Table.Cell>
                                            <Table.Cell>
                                                <Grid columns={2} >
                                                    <Grid.Row>
                                                        <Grid.Column>
                                                            <Form.Field>
                                                                <Button fluid color='blue' onClick={() => this.handleSelect(text)}>Editar</Button>
                                                            </Form.Field>
                                                        </Grid.Column>
                                                        <Grid.Column>
                                                            <Form.Field>
                                                                <Button fluid color='red' onClick={() => this.handleDelete(text)}>Eliminar</Button>
                                                            </Form.Field>
                                                        </Grid.Column>
                                                    </Grid.Row>
                                                </Grid>
                                            </Table.Cell>
                                        </Table.Row>
                                    )
                                )}
                            </Table.Body>
                        </Table>
                    </Grid.Column>
                </Grid.Row>
            </Grid >
        )
    }
}
